.body-main {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-auto-rows: minmax(50px, auto);
    grid-template-areas:
        "hd"
        "main"
        "ft";
}
.main-header {
    grid-area: hd;
}
.main-content {
    grid-area: main;
    min-height: calc(100vh - 106px);
}
.main-footer {
    grid-area: ft;
}
